@mixin nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-position: center;
  background-size: cover;
  border: solid 2px black;
}
@mixin grid {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  margin-top: 50px;
}
@mixin li {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border: solid 5px black;
  border-radius: 50px;
  list-style: none;
  padding: 25px;
  width: 300px;
  margin-bottom: 30px;
}
@mixin title {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 30px;
}
@mixin p_bold {
  color: rgb(198, 180, 12);

  font-weight: bold;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 25px;
}
@mixin result {
  color: rgb(16, 5, 5);
  font-size: 20px;
  font-weight: bold;
  text-transform: initial;
}
